'use client'

import { useState } from 'react'
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import config from '@/data/config.json'

interface Product {
  id: string
  title: string
  price: number
  category: string
}

interface RobloxDeliveryModalProps {
  isOpen: boolean
  onClose: () => void
  product: Product | null
  paymentMethod: string
  transactionId: string
}

export default function RobloxDeliveryModal({ 
  isOpen, 
  onClose, 
  product, 
  paymentMethod,
  transactionId 
}: RobloxDeliveryModalProps) {
  const [step, setStep] = useState<'form' | 'delivery' | 'complete'>('form')
  const [robloxUsername, setRobloxUsername] = useState('')
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleSubmitDelivery = async () => {
    if (!robloxUsername.trim()) {
      alert('Please enter your Roblox username')
      return
    }

    setIsSubmitting(true)

    try {
      // Send Discord webhook
      const webhookData = {
        embeds: [{
          title: "New Pet Delivery",
          color: 0x00ff00,
          fields: [
            { name: "Customer", value: robloxUsername, inline: true },
            { name: "Pet", value: product?.title || 'Unknown', inline: true },
            { name: "Amount", value: `$${product?.price || 0}`, inline: true },
            { name: "Payment Method", value: paymentMethod, inline: true },
            { name: "Transaction ID", value: transactionId, inline: true },
            { name: "Private Server", value: config.delivery.privateServerLink, inline: false },
            { name: "Seller Account", value: config.delivery.robloxSellerUsername, inline: true },
            { name: "Timestamp", value: new Date().toLocaleString(), inline: true }
          ]
        }]
      }

      if (config.delivery.webhookUrl && config.delivery.webhookUrl !== 'https://discord.com/api/webhooks/YOUR_WEBHOOK_URL') {
        await fetch(config.delivery.webhookUrl, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(webhookData)
        })
      }

      setStep('delivery')
    } catch (error) {
      console.error('Failed to send webhook:', error)
      alert('Failed to process delivery. Please contact support.')
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleComplete = () => {
    setStep('complete')
  }

  if (!product) return null

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>
            {step === 'form' && 'Roblox Delivery'}
            {step === 'delivery' && 'Delivery Details'}
            {step === 'complete' && 'Delivery Complete!'}
          </DialogTitle>
        </DialogHeader>

        {step === 'form' && (
          <div className="space-y-4">
            <Card>
              <CardContent className="pt-4">
                <div className="flex justify-between items-center mb-2">
                  <span className="font-medium">{product.title}</span>
                  <Badge variant="secondary">{product.category}</Badge>
                </div>
                <div className="text-2xl font-bold text-primary">${product.price}</div>
                <div className="text-sm text-muted-foreground">Payment: {paymentMethod}</div>
              </CardContent>
            </Card>

            <div>
              <Label htmlFor="robloxUsername">Your Roblox Username</Label>
              <Input
                id="robloxUsername"
                value={robloxUsername}
                onChange={(e) => setRobloxUsername(e.target.value)}
                placeholder="Enter your Roblox username"
                className="mt-1"
              />
            </div>

            <Button 
              onClick={handleSubmitDelivery} 
              disabled={isSubmitting}
              className="w-full"
            >
              {isSubmitting ? 'Processing...' : 'Get Delivery Details'}
            </Button>
          </div>
        )}

        {step === 'delivery' && (
          <div className="space-y-4">
            <Card>
              <CardContent className="pt-4 space-y-3">
                <div>
                  <Label>Private Server Link</Label>
                  <Input 
                    value={config.delivery.privateServerLink} 
                    readOnly 
                    className="mt-1 bg-muted"
                  />
                </div>
                <div>
                  <Label>Seller Account</Label>
                  <Input 
                    value={config.delivery.robloxSellerUsername} 
                    readOnly 
                    className="mt-1 bg-muted"
                  />
                </div>
                <div>
                  <Label>Your Roblox Username</Label>
                  <Input 
                    value={robloxUsername} 
                    readOnly 
                    className="mt-1 bg-muted"
                  />
                </div>
              </CardContent>
            </Card>

            <div className="text-sm text-muted-foreground">
              <p>1. Join the private server using the link above</p>
              <p>2. Look for seller: {config.delivery.robloxSellerUsername}</p>
              <p>3. Trade your pet: {product.title}</p>
            </div>

            <Button onClick={handleComplete} className="w-full">
              I've Received My Pet
            </Button>
          </div>
        )}

        {step === 'complete' && (
          <div className="space-y-4 text-center">
            <div className="text-green-600 text-2xl mb-2">✅</div>
            <h3 className="font-bold text-lg">Delivery Complete!</h3>
            <p className="text-muted-foreground">
              Your pet has been delivered successfully. 
              Transaction ID: {transactionId}
            </p>
            <div className="space-y-2">
              <p className="text-sm text-muted-foreground">
                Please take a screenshot of your pet in-game as proof of delivery.
              </p>
              <Button variant="outline" onClick={onClose}>
                Close
              </Button>
            </div>
          </div>
        )}
      </DialogContent>
    </Dialog>
  )
}
