'use client'

import { useState } from 'react'
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Checkbox } from '@/components/ui/checkbox'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Label } from '@/components/ui/label'
import config from '@/data/config.json'
import RobloxDeliveryModal from './RobloxDeliveryModal'

interface Product {
  id: string
  title: string
  price: number
  originalPrice?: number
  image: string
  category: string
}

interface EnhancedCheckoutModalProps {
  isOpen: boolean
  onClose: () => void
  product: Product | null
}

export default function EnhancedCheckoutModal({ 
  isOpen, 
  onClose, 
  product 
}: EnhancedCheckoutModalProps) {
  const [acceptedTOS, setAcceptedTOS] = useState(false)
  const [selectedPayment, setSelectedPayment] = useState<string>('')
  const [showDelivery, setShowDelivery] = useState(false)
  const [transactionId, setTransactionId] = useState('')

  if (!product) return null

  const savings = product.originalPrice ? product.originalPrice - product.price : 0

  // Determine available payment methods based on payment mode
  const getAvailablePaymentMethods = () => {
    const methods = []
    
    if (config.paymentMode === 'crypto' || config.paymentMode === 'both') {
      if (config.paymentMethods.crypto.enabled) {
        methods.push(...config.paymentMethods.crypto.acceptedCoins)
      }
      if (config.paymentMethods.gcash.enabled) {
        methods.push('GCash')
      }
    }
    
    if (config.paymentMode === 'discord' || config.paymentMode === 'both') {
      methods.push('Discord Server')
    }
    
    return methods
  }

  const availableMethods = getAvailablePaymentMethods()

  const handleCheckout = () => {
    if (!acceptedTOS) {
      alert('Please accept the Terms of Service to continue.')
      return
    }

    if (!selectedPayment) {
      alert('Please select a payment method.')
      return
    }

    // Generate transaction ID
    const newTransactionId = `TXN-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`
    setTransactionId(newTransactionId)

    // Handle different payment flows
    if (selectedPayment === 'Discord Server') {
      // Redirect to Discord
      window.open(config.checkout.discordInvite, '_blank')
      onClose()
    } else {
      // Show delivery modal for crypto/GCash
      setShowDelivery(true)
    }
  }

  const handleDeliveryComplete = () => {
    setShowDelivery(false)
    onClose()
  }

  return (
    <>
      <Dialog open={isOpen} onOpenChange={onClose}>
        <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle className="text-2xl">Complete Your Purchase</DialogTitle>
          </DialogHeader>

          <div className="space-y-6">
            {/* Product Summary */}
            <Card>
              <CardContent className="pt-6">
                <div className="flex gap-4">
                  <img 
                    src={product.image} 
                    alt={product.title}
                    className="w-20 h-20 object-cover rounded-lg"
                  />
                  <div className="flex-1">
                    <h3 className="font-semibold text-lg">{product.title}</h3>
                    <Badge variant="outline" className="mt-1">
                      {product.category === 'minecraft' ? 'Minecraft' : 'Garden & Pets'}
                    </Badge>
                    <div className="flex items-center gap-2 mt-2">
                      <span className="text-2xl font-bold text-primary">
                        ${product.price.toFixed(2)}
                      </span>
                      {product.originalPrice && (
                        <>
                          <span className="text-sm text-muted-foreground line-through">
                            ${product.originalPrice.toFixed(2)}
                          </span>
                          <Badge variant="destructive" className="text-xs">
                            Save ${savings.toFixed(2)}
                          </Badge>
                        </>
                      )}
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Payment Mode Info */}
            <Card>
              <CardContent className="pt-4">
                <h4 className="font-medium mb-2">Payment Mode: {config.paymentMode.toUpperCase()}</h4>
                <p className="text-sm text-muted-foreground">
                  {config.paymentMode === 'crypto' && 'Pay with cryptocurrency for instant delivery'}
                  {config.paymentMode === 'discord' && 'Complete purchase through Discord server'}
                  {config.paymentMode === 'both' && 'Choose your preferred payment method'}
                </p>
              </CardContent>
            </Card>

            {/* Payment Method Selection */}
            {availableMethods.length > 1 && (
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg">Select Payment Method</CardTitle>
                </CardHeader>
                <CardContent>
                  <RadioGroup value={selectedPayment} onValueChange={setSelectedPayment}>
                    {availableMethods.map(method => (
                      <div key={method} className="flex items-center space-x-2 p-3 border rounded-lg">
                        <RadioGroupItem value={method} id={method} />
                        <Label htmlFor={method} className="cursor-pointer">
                          {method}
                          {method === 'Discord Server' && (
                            <span className="text-sm text-muted-foreground ml-2">
                              (Join our Discord server)
                            </span>
                          )}
                          {config.paymentMethods.crypto.acceptedCoins.includes(method) && (
                            <span className="text-sm text-muted-foreground ml-2">
                              (Cryptocurrency)
                            </span>
                          )}
                        </Label>
                      </div>
                    ))}
                  </RadioGroup>
                </CardContent>
              </Card>
            )}

            {/* Terms of Service */}
            <Card>
              <CardContent className="pt-6">
                <div className="flex items-center space-x-3">
                  <Checkbox 
                    id="tos" 
                    checked={acceptedTOS}
                    onCheckedChange={(checked) => setAcceptedTOS(checked as boolean)}
                  />
                  <div className="text-sm">
                    <label htmlFor="tos" className="cursor-pointer">
                      I have read and agree to the{' '}
                      <a 
                        href="/terms" 
                        target="_blank" 
                        className="text-primary hover:underline font-medium"
                      >
                        Terms of Service
                      </a>
                      {' '}and understand that all sales are final.
                    </label>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Action Buttons */}
            <div className="flex gap-3 pt-4">
              <Button 
                variant="outline" 
                onClick={onClose}
                className="flex-1"
              >
                Cancel
              </Button>
              <Button 
                onClick={handleCheckout}
                className="flex-1"
                disabled={!acceptedTOS || (!selectedPayment && availableMethods.length > 1)}
              >
                {availableMethods.length === 1 ? 'Continue' : 'Proceed to Payment'}
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>

      <RobloxDeliveryModal 
        isOpen={showDelivery}
        onClose={handleDeliveryComplete}
        product={product}
        paymentMethod={selectedPayment}
        transactionId={transactionId}
      />
    </>
  )
}
