'use client'

import { Card, CardContent, CardFooter, CardHeader } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import config from '@/data/config.json'

interface Product {
  id: string
  title: string
  description: string
  price: number
  originalPrice?: number
  available: boolean
  featured: boolean
  image: string
  category: string
  features: string[]
}

interface ProductCardProps {
  product: Product
  onPurchase: (product: Product) => void
}

export default function ProductCard({ product, onPurchase }: ProductCardProps) {
  const savings = product.originalPrice ? product.originalPrice - product.price : 0
  const discountPercentage = product.originalPrice 
    ? Math.round(((product.originalPrice - product.price) / product.originalPrice) * 100)
    : 0

  return (
    <Card className={`h-full flex flex-col transition-all duration-300 hover:shadow-lg ${
      product.featured ? 'ring-2 ring-primary/20 shadow-md' : ''
    }`}>
      {/* Image Section */}
      <div className="relative">
        <img 
          src={product.image} 
          alt={product.title}
          className="w-full h-48 object-cover rounded-t-lg"
        />
        
        {/* Badges */}
        <div className="absolute top-3 left-3 flex flex-col gap-2">
          {product.featured && (
            <Badge className="bg-primary text-primary-foreground">
              Featured
            </Badge>
          )}
          {discountPercentage > 0 && (
            <Badge variant="destructive">
              {discountPercentage}% OFF
            </Badge>
          )}
        </div>

        {/* Availability Badge */}
        <div className="absolute top-3 right-3">
          <Badge variant={product.available ? "default" : "secondary"}>
            {product.available ? 'Available' : 'Out of Stock'}
          </Badge>
        </div>
      </div>

      <CardHeader className="pb-3">
        <div className="flex items-start justify-between gap-2">
          <h3 className="font-semibold text-lg leading-tight">{product.title}</h3>
          <Badge variant="outline" className="text-xs whitespace-nowrap">
            {product.category === 'minecraft' ? 'Minecraft' : 'Garden & Pets'}
          </Badge>
        </div>
      </CardHeader>

      <CardContent className="flex-1 pb-3">
        <p className="text-muted-foreground text-sm mb-4 line-clamp-3">
          {product.description}
        </p>

        {/* Features */}
        {product.features && product.features.length > 0 && (
          <div className="space-y-2">
            <h4 className="text-sm font-medium">Features:</h4>
            <ul className="text-xs text-muted-foreground space-y-1">
              {product.features.slice(0, 3).map((feature, index) => (
                <li key={index} className="flex items-center gap-2">
                  <div className="w-1 h-1 bg-primary rounded-full flex-shrink-0"></div>
                  {feature}
                </li>
              ))}
              {product.features.length > 3 && (
                <li className="text-primary text-xs">
                  +{product.features.length - 3} more features
                </li>
              )}
            </ul>
          </div>
        )}
      </CardContent>

      <CardFooter className="pt-0">
        <div className="w-full space-y-3">
          {/* Pricing */}
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <span className="text-2xl font-bold text-primary">
                {config.site.currencySymbol}{product.price.toFixed(2)}
              </span>
              {product.originalPrice && (
                <span className="text-sm text-muted-foreground line-through">
                  {config.site.currencySymbol}{product.originalPrice.toFixed(2)}
                </span>
              )}
            </div>
            {savings > 0 && (
              <Badge variant="outline" className="text-xs">
                Save {config.site.currencySymbol}{savings.toFixed(2)}
              </Badge>
            )}
          </div>

          {/* Purchase Button */}
          <Button 
            onClick={() => onPurchase(product)}
            disabled={!product.available}
            className="w-full"
            size="sm"
          >
            {product.available ? 'Purchase Now' : 'Out of Stock'}
          </Button>

          {product.available && (
            <p className="text-xs text-muted-foreground text-center">
              Instant delivery via Discord
            </p>
          )}
        </div>
      </CardFooter>
    </Card>
  )
}
